import React, {Component} from 'react';
import Navbar, {NavbarText} from "react-bootstrap/Navbar";
import NavbarBrand from "react-bootstrap/NavbarBrand";
import NavbarToggle from "react-bootstrap/NavbarToggle";
import NavbarCollapse from "react-bootstrap/NavbarCollapse";
import Nav from "react-bootstrap/Nav";
import NavLink from "react-bootstrap/NavLink";
import {connect} from "react-redux";
import PropTypes from "prop-types"
import {logout} from "../actions/securityAction";
import {Image} from "react-bootstrap";
import {Link} from "react-router-dom";
import { NavLink as RouterNavLink } from "react-router-dom";

class Header extends Component {
    constructor(props) {
        super(props);
        this.state = {
            security: this.props.security
        }
        this.onLogoutHandler=this.onLogoutHandler.bind(this);
    }

     onLogoutHandler=()=>
    {
     this.props.logout();
    }

    componentWillReceiveProps(nextProps, nextContext) {

        this.setState({security:nextProps.security});
    }

    render() {
        const {validToken,user}=this.state.security;
        return (
            <div>
                <Navbar expand={"lg"} variant={"light"} bg={"light"} fixed={"top"}>
                    <NavbarBrand className="font-libre" href={"/"}>
                        <Image src={require("../assests/icons/brand.png")}
                 height="30px" width="38px" />
                    </NavbarBrand>
                    <Navbar.Text className="d-none d-sm-block font-libre ">
                        <strong>Personal Project Management</strong>
                    </Navbar.Text>
                    <NavbarToggle aria-controls={"collapseableRegion"}/>
                    <NavbarCollapse id={"collapseableRegion"}>

                            {
                                (validToken&&user) ? (    <Nav className="ml-md-auto font-libre ">
                                    <NavLink >
                                        <Image  className="mb-2" src={require("../assests/icons/dashboard.png")}
                                                height="25px" width="25px"/>
                                        <RouterNavLink to="/dashboard">Dashboard</RouterNavLink>
                                    </NavLink>
                                        <NavLink href={"/"} onClick={this.onLogoutHandler.bind(this)}>
                                            <Image className="mb-2" src={require("../assests/icons/logout.png")}
                                                   height="25px" width="30px"  />
                                            Logout
                                        </NavLink>
                                </Nav>
                                ) : (
                                    <Nav className={"ml-md-auto"}>
                                    <NavLink>
                                        <RouterNavLink to="/login"> Login</RouterNavLink>
                                    </NavLink>
                                <NavLink  >
                                    <RouterNavLink to="/register">Register</RouterNavLink>
                                </NavLink>
                                    </Nav>
                                )
                            }

                    </NavbarCollapse>
                </Navbar>
            </div>
        );
    }
}
Header.propTypes={
    logout:PropTypes.func.isRequired,
    security:PropTypes.object.isRequired
};

const MapStateToProps = state =>({
    security:state.security
});
export default connect(MapStateToProps,{logout})(Header);