import React, {Component} from 'react';
import Jumbotron from "react-bootstrap/Jumbotron";
import '../assests/css/fonts.css'
import '../assests/css/landing.css'
import {Link, withRouter} from "react-router-dom";
import {Image} from "react-bootstrap";
import mapStateToProps from "react-redux/lib/connect/mapStateToProps";
import {connect} from "react-redux";
import PropTypes from "prop-types";
import {createNewUser, login, logout} from "../actions/securityAction";
import {setJWTToken} from "../securityUtils";
import jwt_decode from "jwt-decode";
import store from "../store";
import {SET_CUREENT_USER} from "../actions/types";
import Header from "./Header";
class Landing extends Component {
    constructor(props) {
        super(props);
        this.state={
            security:{},
            jwt:this.props.jwt
        }
    }
    componentWillReceiveProps(nextProps, nextContext) {
        this.setState({security:nextProps.security});
    }

    render() {
        const {jwt}=this.state;
        let isAuth=false;
        if( jwt) {
            const decoded_jwtToken = jwt_decode(jwt);
            const currentTime = Date.now() / 1000;
            if (decoded_jwtToken.exp > currentTime) {
            isAuth=true;
            }
        }
        return (
            <div className="main-background">
                <Header/>
                <div className="row text-light">
                <Jumbotron className="row jumbotron-home justify-content-sm-center  text-center">
                <div className="container pl-4 text-light bg-transparent">
                        <h1 className="font-crimson display-4 text-light text-center pt-5"> Welcome to Personal Project Managaement Tool</h1>
                        <h2 className="font-roboto font-weight-bold m-4 ">Now Manage Your Project Effortlessly</h2>

                        {!(isAuth) && (
                            <div className=" bg-transparent">
                                <h1 className="font-plex mt-4 mb-4">Be More Productive</h1>
                        <h3 className="text-light font-italic text-center font-crimson">
                           Sign up Today
                        </h3>
                        <hr className="hr-line"/>
                        <Link to="/register" className="btn btn-lg btn-primary mr-2">
                            Sign Up
                        </Link>
                        <Link to="/login" className="btn btn-lg btn-secondary mr-2">
                            Login
                        </Link>
                            </div>
                             )}
                    {
                        isAuth&&(
                            <div className=" bg-transparent">
                                <h1 className="font-plex mt-4 mb-4">Be More Productive</h1>
                                <hr className="hr-line"/>
                                <Link to="/dashboard" className="btn btn-lg btn-primary mr-2">
                                    Back To Dashboard
                                </Link>
                            </div>
                        )
                    }
            </div>
                </Jumbotron>
                </div>
                <div className="row pt-6 display-4 text-center flex-wrap font-crimson flex mb-5">
                    <div className="col-sm-12 pb- 5">
                        What do we Offer ..
                    </div>
                </div>
                <div className="row flex flex-wrap text-black-50 text-size">
                    <div className="col-sm-4 shadow-sm  font-crimson border-0">
                        <div className="card bg-transparent border-0 text-size">
                            <div className="card-header  bg-transparent border-0">
                            PLAN
                        </div>
                            <div className="card-img">
                                <Image className="add-image" src={require("../assests/icons/plan.png")}
                                       height="90px" width="90px"  />
                            </div>
                        <div className="card-body">
                            Plan Your Projects Efficiently
                        </div>
                        </div>
                        </div>

                    <div className="col-sm-4 shadow-sm  font-crimson border-0 text-size">
                        <div className="card bg-transparent border-0">
                            <div className="card-header bg-transparent border-0">
                                TRACK
                            </div>
                            <div className="card-img">
                                <Image className="add-image" src={require("../assests/icons/track.png")}
                                       height="90px" width="90px"  />
                            </div>
                            <div className="card-body font-crimson ">
                                Keep  Track  Of  Ongoing Projects
                            </div>
                        </div>
                    </div>

                    <div className="col-sm-4 shadow-sm  font-crimson border-0 text-size">
                        <div className="card bg-transparent border-0">
                            <div className="card-header  bg-transparent border-0 ">
                                CUSTOMIZE
                            </div>
                            <div className="card-img">
                                <Image className="add-image" src={require("../assests/icons/customize.png")}
                                       height="90px" width="90px"  />
                            </div>
                            <div className="card-body">
                               Customize and Prioritize your tasks
                            </div>
                        </div>
                    </div>
                </div>
                <div className="row"><hr/></div>
            </div>
        );
    }
}
Landing.propTypes={
    security:PropTypes.object.isRequired
}
const MapStateToProps = state =>({
    security:state.security
});
export default connect(MapStateToProps)(Landing);