import React from 'react';
import logo from './logo.svg';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import './assests/css/fonts.css'
import './assests/css/addproject.css'
import Dashboard from "./components/Dashboard";
import {Provider} from "react-redux";
import {BrowserRouter as Router, Route,withRouter,HashRouter} from "react-router-dom";
import store from "./store";
import Header from "./components/Header";
import CreateProject from "./components/CreateProject";
import AddProject from "./components/AddProject";
import UpdateProject from "./components/UpdateProject";
import ProjectBoard from "./components/ProjectBoard";
import AddProjectTask from "./components/AddProjectTask";
import UpdateProjectTask from "./components/UpdateProjectTask";
import Landing from "./components/Landing";
import Login from "./components/Login";
import Register from "./components/Register";
import jwt_decode from "jwt-decode";
import {setJWTToken} from './securityUtils'
import {SET_CUREENT_USER} from "./actions/types";
import {logout} from "./actions/securityAction";
import SecuredRoute from './components/SecuredRoute'
import Switch from "react-bootstrap/cjs/Switch";

const jwtToken = localStorage.jwtToken;
if (jwtToken) {
    setJWTToken(jwtToken);
    const decoded_jwtToken = jwt_decode(jwtToken);
    store.dispatch({
        type: SET_CUREENT_USER,
        payload: decoded_jwtToken
    });
    const currentTime = Date.now() / 1000;
    if (decoded_jwtToken.exp < currentTime) {
        store.dispatch(logout());
        window.location.href = '/';
    }
}

function App() {
    return (
        <Provider store={store}>
            <HashRouter >
                <Header/>
                <div className="app">
                    <Route exact path="/" render={(props) => <Landing {...props} jwt={jwtToken}/>}/>
                    <Route  exact path="/login" component={Login}></Route>
                        <Route  exact path="/register" component={Register}></Route>
                        <SecuredRoute  path="/dashboard" component={Dashboard}/>
                        <SecuredRoute  path="/addproject" component={AddProject}/>
                        <SecuredRoute  path="/updateproject/:id" component={UpdateProject}/>
                        <SecuredRoute  path="/projectBoard/:id" component={ProjectBoard}/>
                        <SecuredRoute  path="/addProjectTask/:id" component={AddProjectTask}/>
                        <SecuredRoute  path="/updateProjectTask/:backlog_id/:pt_id" component={UpdateProjectTask}/>
                </div>
            </HashRouter>
        </Provider>
    );
}

export default App;
