import React, {Component} from 'react';
import '../assests/css/fonts.css'
import '../assests/css/login.css'
import {Link} from "react-router-dom";
import {connect} from "react-redux";
import PropTypes from "prop-types"
import classnames from "classnames";
import {login} from "../actions/securityAction";
import Spinner from "react-bootstrap/Spinner";
import Header from "./Header";

class Login extends Component {
    constructor(props) {
        super(props);

        this.state=
            {
                username: "",
                password: "",
                errors:{},
                loading:false
            }
        this.onInputChange=this.onInputChange.bind(this);
        this.onSubmitHandler=this.onSubmitHandler.bind(this);

    }
    onInputChange= (event)=>
    {
        this.setState({[event.target.name] : event.target.value});
    }
    componentWillReceiveProps(nextProps, nextContext) {
        if (nextProps.errors)
            this.setState({errors: nextProps.errors, loading: false});
        if (nextProps.security.validToken && nextProps.security.user) {
            this.setState({loading:false});
            this.props.history.push("/dashboard")
        }
    }
    componentDidMount() {
        if( this.props.security.validToken)
        {
            this.props.history.push("/dashboard");
        }
    }

    onSubmitHandler=(event)=> {
        event.preventDefault();
        this.setState({loading:true});
        const LoginRequest =
            {
                username: this.state.username,
                password: this.state.password,

            }
        this.props.login(LoginRequest,this.props.history);
    }
    render() {
        const {errors}=this.state;
        return (
            <div className="login-body ">

                {errors.server&&!this.state.loading&&(<div className="row alert alert-danger"> .
                    <p className=" justify-content-center">Please wait while the server is Starting .It May take 40-60 seconds. Then try again</p></div>)}
                <div className="container ">
                    <div className="row justify-content-sm-center">
                    <div className="col-sm-12 col-md-8 col-lg-6 ">
                    <form className="shadow-lg font-plex">
                        <legend className="text-center font-libre display-4 pl-2 text-form"><strong> Login </strong></legend>
                        <div className="m-3 form-group">
                            <label htmlFor="username" className="text-left pl-4 form-row">Username</label>
                            <input type="text"
                                   className={classnames("form-control form-control-lg ",{
                                       "is-invalid":errors.username})} value={this.state.username} onChange={this.onInputChange} name="username" placeholder="Registered Email is your username"/>
                            {errors.username && (<div className="h-1 text-sm-left invalid-feedback font-weight-bolder">
                                {errors.username}
                            </div>)}
                        </div>
                        <div className=" m-3 form-group">
                            <label htmlFor="password" className="text-left pl-4 form-row">Password</label>
                            <input type="password"
                                   className={classnames("form-control form-control-lg ",{
                                       "is-invalid":errors.password})}onChange={this.onInputChange}    value={this.state.password} onChange={this.onInputChange} name="password" placeholder="Password"/>
                            {errors.password && (<div className="h-1 text-sm-left invalid-feedback font-weight-bolder">
                                {errors.password}</div>)}
                        </div>
                        <div className="text-center mt-4 ">
                            <button type="submit" onClick={this.onSubmitHandler} className="btn mb-2 text-secondary bg-light btn-outline-dark">Login</button>
                        </div>
                        {this.state.loading&& <div className="form-row justify-content-center ">
                            <Spinner animation={"border"} variant="primary "/>
                        </div>}
                        <hr className="hr-line"/>
                        <div className=" small m-4 text-muted text-center ">
                            New User!!
                            <Link className="p-4" to="/register"> Register Here</Link>
                        </div>
                    </form>
                </div>

                </div>
                </div>
            </div>
        );
    }
}
Login.propTypes={

    errors:PropTypes.object.isRequired
}
const mapStateToProps=(state)=>(
{   security:state.security,
    errors:state.errors

});
export default connect(mapStateToProps ,{login})(Login);