import React from "react";
import axios from "axios";
import {GET_ERRORS, GET_ERRORS_SERVER, SET_CUREENT_USER} from "./types";
import {setJWTToken} from '../securityUtils'
import jwt_decode from 'jwt-decode';
import {SERVER_URL} from "./Constatnts";
export const createNewUser=(newUser,history)=> async dispatch=>
{
    try
    {
await axios.post(`${SERVER_URL}/api/users/register`,newUser,setTimeout(null,3000));
history.push("/login");
dispatch(
    {
        type:GET_ERRORS,
        payload:""
    }
);
    }
    catch (error)  {
        const ans=(error.response===undefined)?false:true;
        console.log(ans,error.response)
        if(ans) {
            dispatch({
                type: GET_ERRORS,
                payload: error.response.data
            });
        }

        else{
            dispatch({
                type: GET_ERRORS_SERVER,
                payload: { server:"timeout"}
            });
        }
    }

}


export const login=(LoginRequest,history)=> async dispatch=>
{
    //post-> login request;
    try{
        const res =await axios.post(`${SERVER_URL}/api/users/login`,LoginRequest,setTimeout(null,3000));
        //extract token from res.data
        const {token}=res.data;
        //store the token in local Stroage
        localStorage.setItem("jwtToken",token)
        //set our token in header
        setJWTToken(token);
        //decode toke n on string
        const decoded=jwt_decode(token);
        //dispatch to our security reducer

        dispatch({
          type:SET_CUREENT_USER,
          payload:decoded
        });

    }
    catch (error)  {
        const ans=(error.response===undefined)?false:true;
        console.log(ans,error.response)
        if(ans) {
            dispatch({
                type: GET_ERRORS,
                payload: error.response.data
            });
        }

        else{
            dispatch({
                type: GET_ERRORS_SERVER,
                payload: { server:"timeout"}
            });
        }
    }
}
export const logout=() =>dispatch=>
{
    localStorage.removeItem("jwtToken")
    setJWTToken(false);
    dispatch({
        type:SET_CUREENT_USER,
        payload:{}  //valid token will be false on no payload;
    });

}