import React, {Component} from 'react';
import '../assests/css/fonts.css'
import '../assests/css/projectBoard.css'
import {Image, ListGroup} from "react-bootstrap";
import {Link} from "react-router-dom";
import Backlog from "./Backlog";
import {connect}from "react-redux";
import PropTypes from "prop-types"
import {addProjectTask, getBacklog} from "../actions/backlogActions";
import mapStateToProps from "react-redux/lib/connect/mapStateToProps";
import {getProject} from "../actions/projectActions";
import ListGroupItem from "react-bootstrap/ListGroupItem";
import Spinner from "react-bootstrap/Spinner";
class ProjectBoard extends Component {
    constructor(props) {
        super(props);
        this.state={
            errors:{},
            hasError:false,
            project:{},
            loading:true
        }
    }
    componentDidMount() {
        const {id}=this.props.match.params;
        this.props.getBacklog(id);
        this.props.getProject(id);
    }
    componentWillReceiveProps(nextProps, nextContext) {
        if (nextProps.errors) {
            this.setState({errors: nextProps.errors,
                hasError:true,loading:false});
        }
        if( nextProps.project)
        this.setState({project:nextProps.project,loading:false});
    }

    render() {

        const {id}=this.props.match.params;
        const {project_tasks}=this.props.backlog;
        const {errors,project}=this.state;
        let startDate=new Date(project.start_date);
        startDate=startDate.getDate()+"/"+(startDate.getMonth()+1)+"/"+(startDate.getFullYear());
        let endDate=new Date(project.end_date);
        endDate=endDate.getDate()+"/"+(endDate.getMonth()+1)+"/"+(endDate.getFullYear());
        return (
            <div>
                {  Object.keys(errors).length===0&&(<div className="main-content ">
                        <div className="container justify-content-center ">
                            <div className="row justify-content-end">
                                <div className="col-sm-4">
                                    <Link to={`/dashboard`} className="btn bg-dark text-light shadow-lg mb-3 text-left font-mont pr-2 border-all
                    border-info">
                                        <Image className=" p-1" src={require("../assests/icons/back.png")} height="30px" width="30px "/> Back To Dashboard
                                    </Link>
                                </div>
                                <div className="col-sm-8 justify-content-end">
                                    <fieldset className="col-sm-6 justify-content-center">
                                        <div className="text-justify   border-all ">
                                            {this.state.loading===true?( <div className="row justify-content-center ">
                                                <Spinner animation={"border"} variant="primary "/></div>):(<div>
                                            <legend className="font-plex text-center">Project Details</legend>
                                            <ListGroup className=" justify-content-end ">
                                                <ListGroupItem className="p-2 font-plex text-dark text-size">{`Project Board for  ${project.projectIdentifier} `}
                                                </ListGroupItem>
                                                <ListGroupItem className=" p-2 text-info font-plex text-size " > {`Start Date of Project:  ${startDate} `}</ListGroupItem>
                                                <ListGroupItem className=" p-2  font-plex text-danger  text-size ">{`End Date of Project:  ${endDate} `}</ListGroupItem>
                                            </ListGroup></div>
                                                )}
                                        </div>
                                    </fieldset>
                                </div>
                            </div>
                            <div className="row mt-4 justify-content-end">
                <span className="col-sm-4 justify-content-start">
                    <Link to={`/addProjectTask/${id}`} className="btn bg-dark text-light shadow-lg mb-3 text-left  font-mont pr-2 border-all
                    border-info">
                        <Image className=" p-1" src={require("../assests/icons/add.png")}  height="30px"  width="30px "/> Create Project Task
                    </Link>
                </span>
                                <hr className={"hr-line"}/>
                            </div>
                            { project_tasks.length>=1&&(<div>
                                <Backlog project_tasks_prop={project_tasks} />
                            </div> )}
                            { project_tasks.length<1&&(
                                <div className="row justify-content-sm-center">
                                    <div className="alert col-sm-8 alert-info font-weight-bolder">
                                        No Project Exist Currently for This Id
                                    </div>
                                </div>)}
                        </div>
                    </div>

                )}
                {  !(Object.keys(errors).length===0) &&(<div className="top-gap">
                    <div className="row justify-content-sm-center">
                        <div className="alert col-sm-8 alert-danger font-weight-bolder">
                            This Project Does not exist
                        </div>
                    </div>
                </div>)}
            </div>
        );
    }
}

ProjectBoard.propTypes={
    getBacklog:PropTypes.func.isRequired,
    backlog:PropTypes.object.isRequired,
    errors:PropTypes.object.isRequired,
    getProject:PropTypes.func.isRequired,
    project:PropTypes.object.isRequired
}
const MapStateToProps=(state)=>({
    backlog:state.backlog,
    errors:state.errors,
    project:state.project.project
});
export default connect(MapStateToProps,{getBacklog,getProject})(ProjectBoard);