import React, {Component} from 'react';
import '../assests/css/fonts.css'
import  '../assests/css/register.css'
import {createNewUser} from "../actions/securityAction";
import {connect} from "react-redux";
import PropTypes from "prop-types"
import classnames from "classnames";
import {Link} from "react-router-dom";
import Spinner from "react-bootstrap/Spinner";
import Header from "./Header";

class Register extends Component {
    constructor(props) {
        super(props);
        this.state=
        {

            username: "",
            fullName: "",
            password: "",
            confirmPassword: "",
            errors:{},
            loading:false
        }
        this.onInputChange=this.onInputChange.bind(this);
        this.onSubmitHandler=this.onSubmitHandler.bind(this);
    }
    onInputChange= (event)=>
    {
        this.setState({[event.target.name] : event.target.value});
    }
    componentDidMount() {
        if( this.props.security.validToken)
        {
            this.props.history.push("/dashboard");
        }
    }

    componentWillReceiveProps(nextProps, nextContext) {
        if( nextProps.errors)
            this.setState({errors:nextProps.errors,loading:false});
    }

    onSubmitHandler=(event)=> {
        event.preventDefault();
        this.setState({loading:true});
        const newUser =
            {
                username: this.state.username,
                fullName: this.state.fullName,
                password: this.state.password,
                confirmPassword: this.state.confirmPassword
            }

        this.props.createNewUser(newUser,this.props.history);
    }
    render() {
    const {errors}=this.state;
        return (
            <div className="top-gap register-body">
                {errors.server&&!this.state.loading&&(<div className="row alert alert-danger"> .
                    <p className=" justify-content-center">Please wait while the server is Starting .It May take 40-60 seconds. Then try again</p></div>)}

                    <div className="container">
                        <div className="row ">
                            <div className="col-md-8  col-lg-6 col-sm-12 m-auto text-left shadow-lg">
                                <form action="/"  method="POST" className="card-body">
                                    <legend className="text-center text-form"><strong> Register </strong></legend>
                                    <div className="form-group">
                                        <label htmlFor="fullName">Name</label>
                                        <input type="text" value={this.state.fullName}
                                               className={classnames("form-control form-control-lg ",{
                                                   "is-invalid":errors.fullName})} onChange={this.onInputChange}  name="fullName" placeholder="Enter Full Name"/>
                                        {errors.fullName && (<div className="h-1 text-sm-left invalid-feedback font-weight-bolder">
                                            {errors.fullName}

                                    </div>)}
                                    </div>
                                    <div className="form-group">
                                        <label htmlFor="username">Email </label>
                                        <input type="email"  className={classnames("form-control form-control-lg ",{
                                            "is-invalid":errors.username})}    value={this.state.username} onChange={this.onInputChange}  name="username" placeholder="Enter email .This will be your username"/>
                                        {errors.username && (<div className="h-1 text-sm-left invalid-feedback font-weight-bolder">
                                            {errors.username}
                                        </div>)}

                                    </div>
                                    <div className="form-group">
                                        <label htmlFor="password">Password</label>
                                        <input type="password"
                                               className={classnames("form-control form-control-lg ",{
                                                   "is-invalid":errors.password})}onChange={this.onInputChange}     value={this.state.password}  name="password" placeholder="Password"/>
                                        {errors.password && (<div className="h-1 text-sm-left invalid-feedback font-weight-bolder">
                                            {errors.password}

                                        </div>)}

                                    </div>
                                    <div className="form-group">
                                        <label htmlFor="confirmPassword">Confirm Password</label>
                                        <input type="password"
                                               className={classnames("form-control form-control-lg ",{
                                                   "is-invalid":errors.confirmPassword})}  onChange={this.onInputChange} value={this.state.confirmPassword} name="confirmPassword" placeholder="Confirm Password"/>
                                    </div>
                                    {errors.confirmPassword && (<div className="h-1 text-sm-left invalid-feedback font-weight-bolder">
                                        {errors.confirmPassword}
                                    </div>)}
                                    <div className="text-center ">
                                        <button type="submit" onClick={this.onSubmitHandler} className="btn mb-2 text-secondary bg-light btn-outline-dark">Register</button>
                                    </div>
                                    {this.state.loading&& <div className="form-row justify-content-center ">
                                        <Spinner animation={"border"} variant="primary "/>
                                    </div>}
                                    <hr/>
                                    <div className=" small text-muted text-center">
                                        Already Have an Account
                                        <Link className="ml-2" to="/login"> Login Here</Link>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>

        );
    }
}
Register.propTypes={
    createNewUser:PropTypes.func.isRequired,
    errors:PropTypes.object.isRequired,
    security:PropTypes.object.isRequired
}
const MapStateToProps=(state)=>(
    {
        errors:state.errors,
        security:state.security
    }
);
export default connect(MapStateToProps,{createNewUser})(Register);